
html, body  {
    background-color: #21232A;
    color: white; 
    font-family: Helvetica, Arial, sans-serif;
    font-size: 1.3rem;
    padding: 70px;
}

input {
    color: grey;
    font-size: 1.2rem;
    width: 100%;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
  
.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}
  
.card p {
    padding: 5px;
    text-align: center;
}
  
.card img {
    max-height: 300px;
    width: 400px;
    margin: auto;
    display: block;
}
  